<template>
  <div class="integer-input-config">
    <Form :label-width="120">
      <div class="config"
           v-for="(item, index) in currentValue" :key="index">
        <!-- 别名 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'alias' && cType !== 1">
          <Input v-model="item.configData.configData"></Input>
        </Form-item>
        <!-- 最大值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'maxLength'">
          <InputNumber v-model="maxLengthDefaultVal"
                           @on-change="numberChange($event, 'maxLength')"></InputNumber>
                           默认值为Infinity
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
        <!-- 最小值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'minLength'">
          <InputNumber type="text"
                           v-model="minLengthDefaultVal"
                           @on-change="numberChange($event, 'minLength')"></InputNumber>
                           默认值为-Infinity
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
        <!-- 输入提示 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'placeholder'">
          <Input v-model="item.configData.configData"></Input>
          <!-- {{ item.configData }} -->
        </Form-item>
        <!-- 默认值 -->
        <Form-item :label="item.name"
                   v-if="item.code === 'value'">
          <InputNumber v-model="valueDefaultVal"
                           @on-change="numberChange($event, 'value')"></InputNumber>
          <!-- {{ item.configData }} -->
          <Row style="margin-top: 10px">
            <Col span="24">
            <Editor v-model="item.configData.otherConfig" :sourceType="2"/>
            </Col>
          </Row>
        </Form-item>
      </div>
    </Form>
  </div>
</template>

<script>
import Editor from '@/components/editor'

export default {
  name: 'integer-input-config',
  components: {
    Editor
  },
  props: {
    cType: {
      type: Number,
      require: true
    },
    configs: {
      type: Array,
      require: true
    }
  },
  watch: {
    currentValue (newValue, oldValue) {
      this.$emit('input', {
        value: this.currentValue
      })
    }
  },
  data () {
    return {
      uuid: '00000000-0000-0000-0000-0000000000015',
      currentValue: null,
      maxLengthN: null,
      maxLengthDefaultVal: null,
      minLengthN: null,
      minLengthDefaultVal: null,
      valueN: null,
      valueDefaultVal: null
    }
  },
  created () {
    this.currentValue = this.deepCopy(this.configs)
    for (const n in this.currentValue) {
      if (this.currentValue[n].code === 'value') {
        this.valueN = n
        if (this.currentValue[n].configData.configData || this.currentValue[n].configData.configData === 0 || this.currentValue[n].configData.configData === '0') {
          this.valueDefaultVal = Number(this.currentValue[n].configData.configData)
        } else {
          this.valueDefaultVal = this.currentValue[n].configData.configData
        }
      } else if (this.currentValue[n].code === 'maxLength') {
        this.maxLengthN = n
        console.log(this.currentValue[n].configData.configData)
        if (this.currentValue[n].configData.configData || this.currentValue[n].configData.configData === 0 || this.currentValue[n].configData.configData === '0') {
          this.maxLengthDefaultVal = Number(this.currentValue[n].configData.configData)
        } else {
          this.maxLengthDefaultVal = this.currentValue[n].configData.configData
        }
      } else if (this.currentValue[n].code === 'minLength') {
        this.minLengthN = n
        if (this.currentValue[n].configData.configData || this.currentValue[n].configData.configData === 0 || this.currentValue[n].configData.configData === '0') {
          this.minLengthDefaultVal = Number(this.currentValue[n].configData.configData)
        } else {
          this.minLengthDefaultVal = this.currentValue[n].configData.configData
        }
      }
    }
  },
  methods: {
    numberChange (val, name) {
      this.currentValue[this[name + 'N']].configData.configData = val
    }
  }
}
</script>

